import {
  FormControl,
  FormControlLabel,
  FormLabel,
  Grid,
  Radio,
  RadioGroup,
} from "@material-ui/core";
import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { getPot, setPot } from "../../features/choices/choicesSlice";

const ComplicationsRadio = () => {
  const dispatch = useDispatch();
  const pot = useSelector(getPot);

  const handleChange = (e) => {
    dispatch(setPot(e.target.value));
  };

  return (
    <Grid container justify="center">
      <FormControl component="fieldset">
        <FormLabel component="legend">
          Choose whether you would like an extra Perk or Talent.
        </FormLabel>
        <RadioGroup
          style={{ justifyContent: "space-around" }}
          row
          aria-label="perk_or_talent"
          name="perk_or_talent1"
          value={pot}
          onChange={handleChange}
        >
          <FormControlLabel value="perk" control={<Radio />} label="Perk" />
          <FormControlLabel value="talent" control={<Radio />} label="Talent" />
        </RadioGroup>
      </FormControl>
    </Grid>
  );
};

export default ComplicationsRadio;
