import {
  Divider,
  Grid,
  Typography,
  GridList,
  GridListTile,
} from "@material-ui/core";
import { find, get } from "lodash";
import React from "react";
import { useDispatch, useSelector } from "react-redux";
import Card from "../../components/card";
import {
  getComplications,
  getComplicationsTitles,
  getPot,
  updateComplications,
} from "../../features/choices/choicesSlice";
import { getComplictionsSection } from "../../features/data/dataSlice";
import ComplicationsRadio from "./ComplicationsRadio";

const ComplicationsSection = () => {
  const dispatch = useDispatch();
  const pot = useSelector(getPot);
  const { title, description, choices } = useSelector(getComplictionsSection);
  const currentComplicationsTitles = useSelector(getComplicationsTitles);
  const currentComplications = useSelector(getComplications);

  return (
    <Grid container spacing={4}>
      <Grid item xs={12}>
        <Typography>{title}</Typography>
      </Grid>
      <Grid item xs={12}>
        <Typography>
          {description.map((sec, ind) => {
            return (
              <span key={`desc-${ind}`}>
                {sec}
                {ind !== description.length - 1 && <br />}
                {ind !== description.length - 1 && <br />}
              </span>
            );
          })}
        </Typography>
      </Grid>
      <Grid item xs={12}>
        <ComplicationsRadio />
      </Grid>
      <Grid item xs={12}>
        <Grid container>
          <GridList>
            {choices.map((choice, index) => {
              return (
                <GridListTile key={`grid-list-tile-${index}`}>
                  <Card
                    handleClick={() =>
                      dispatch(
                        updateComplications({
                          pot,
                          ...choice,
                        })
                      )
                    }
                    picked={currentComplicationsTitles.includes(choice.title)}
                    perkOrTalent={get(
                      find(currentComplications, { title: choice.title }),
                      "pot"
                    )}
                    {...choice}
                  />
                </GridListTile>
              );
            })}
          </GridList>
        </Grid>
      </Grid>
    </Grid>
  );
};

export default ComplicationsSection;
